(function() {
    'use strict';

    angular
        .module('oncoscape')
        .directive('osHeader', header);

    /** @ngInject */
    function header() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/header/header.html',
            scope: {},
            controller: HeaderController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function HeaderController(osApi, $stateParams, $state, $timeout) {

            
            osApi.onDataSource.add(function(){
                $timeout(function(){
                    vm.showTools = true;
                });
            });

            var userApi = osApi.getUserApi();
            userApi.onLogin.add(function(){
                $timeout(function(){
                    vm.showMenu = true;
                });
            })
            userApi.onLogout.add(function(){
                $timeout(function(){
                    vm.showMenu = false;
                });
            })
           
            var vm = this;
            vm.showMenu = false;
            vm.showTools = false;
              
            vm.toolsClick = function(){
                $state.go("tools", {
                    datasource: osApi.getDataSource()
                });
            };
            
            vm.cohortClick = function() {
                osApi.toggleFilter();
            };
            
            vm.logoutClick = function(){
               userApi.logout();
               $state.transitionTo("landing");
            }
        }
    }

})();
(function() {
    'use strict';

    angular
        .module('oncoscape')
        .directive('osTray', tray);

    /** @ngInject */
    function tray() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/tray/tray.html',
            scope: {
                caption: '@',
                content: '@',
                change: '&'

            },
            controller: TrayController,
            controllerAs: 'vm',
            bindToController: true,
            replace: true,
            transclude: true
        };

        return directive;

        /** @ngInject */
        function TrayController($timeout) {


            var vm = this;
            vm.trayClass = Math.random().toString(36).substring(3);
            vm.iconClass = Math.random().toString(36).substring(3);
            

            var isLocked = true;
            vm.toggle = function(){
                var elTray = angular.element("."+vm.trayClass);
                var elIcon = angular.element("."+vm.iconClass);
                var elContent = angular.element("."+vm.content);

                isLocked = !isLocked;
                elIcon
                    .addClass(isLocked ? 'fa-lock' : 'fa-unlock-alt')
                    .removeClass(isLocked ? 'fa-unlock-alt' : 'fa-lock');

                elTray.attr("locked", isLocked ? "true" : "false");

                if (isLocked) {
                    elContent
                        .removeClass("tray-content-block-collapsed");
                    elTray
                        .unbind("mouseover", mouseOver)
                        .unbind("mouseout", mouseOut)
                        .removeClass("tray-collapsed");
                    $timeout(function(){
                        vm.change();
                    });
                        
                } else {
                    elContent
                        .addClass("tray-content-block-collapsed");
                    elTray
                        .addClass("tray-collapsed")
                        .bind("mouseover", mouseOver)
                        .bind("mouseout", mouseOut);
                    $timeout(function(){
                        vm.change();
                    });
                }
            };

            

            var mouseOver = function(){
                angular.element("."+vm.trayClass)
                    .removeClass("tray-collapsed");
            }
            var mouseOut = function(){
                angular.element("."+vm.trayClass)
                    .addClass("tray-collapsed");
            }
        }
    }

})();



